html, body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: Lucida Grande, Lucida, Verdana, sans-serif;
  color: #000;
  background: #000;
  background: url("/img/bg.png");
  border-top: 5px solid #000;
}

ul, li {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
  margin: 0 auto;
}

body > header {
  position: relative;
  background: #fff url("/img/neo.png") no-repeat;
  background-position: top right;
  background-size: 180px;
  height: 197px;
  line-height: 60px;
}

header {
  text-align: center;
  text-shadow: none;
}

header li {
  display: inline-block;
}

header img {
  display: inline-block;
  vertical-align: middle;
}

header h1 {
  font-family: 'Press Start 2P', cursive;
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  margin: 0;
  font-size: 1.5rem;
  z-index: 2;
  position: relative;
}

header .header__doge {
  position: absolute;
  top: 0;
  left: 10px;
  color: red;
  line-height: 1;
  padding: 2px;
}

header .header__nerd {
  position: absolute;
  top: 50px;
  left: 10%;
  font-size: 2rem;
  color: #60da00;
  text-shadow: 1px 0px 10px #09F224;
  outline: 0 none;
  font-family: "Comic Sans MS", cursive, sans-serif;
  line-height: 1;
}

header .header__nerd span {
  font-size: 1rem;
}

header .header__absolute {
  position: absolute;
  font-size: 10px;
  line-height: 12px;
  top: 5%;
  right: 20%;
  width: 100px;
  font-weight: 700;
}

header .header__pikachu {
  height: 50px;
  position: absolute;
  bottom: 0;
  right: 14px;
}

header .header__otter {
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 10%;
}

header .header__glitter-cat {
  height: 80px;
  position: absolute;
  bottom: 0;
  right: 10%;
}

header .header__thumb {
  height: 50px;
  position: absolute;
  top: 47px;
  right: 164px;
}

header .header__facebook {
  height: 50px;
  position: absolute;
  top: 5px;
  left: 25%;
  border: 2px solid #0554be;
}

header .header__linkedin {
  position: absolute;
  left: 10px;
  top: 30px;
  font-size: 12px;
  line-height: 1.2;
  color: #000;
  width: 100px;
  text-align: left;
}

header .header__linkedin:hover {
  background: none;
  text-decoration: underline;
}

header .header__click {
  position: absolute;
  left: 22px;
  bottom: 0px;
}

header .header__click:hover {
  background: none;
}

header .header__random {
  width: 100%;
  overflow: hidden;
  height: 40px;
  background: black;
}

header .header__random ul {
  display: inline-flex;
  line-height: 40px;
  padding: 1px;
  text-align: center;
  width: 100%;
  height: 40px;
  overflow: hidden;
}

header .header__random ul li {
  display: inline-block;
  display: inline-flex;
}

header .header__random ul li img {
  display: inline-block;
}

header nav {
  background: #00B9FF;
  text-align: center;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-weight: bold;
  height: 60px;
}

header nav a {
  color: #fff;
  text-decoration: none;
}

header nav a:hover {
  background: black;
  text-decoration: none;
  color: yellow;
}

header nav a:hover i {
  color: yellow;
}

header nav ul {
  display: inline-flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #6E67BE), color-stop(1, #F516D7));
  background-image: -o-linear-gradient(bottom, #6E67BE 0%, #F516D7 100%);
  background-image: -moz-linear-gradient(bottom, #6E67BE 0%, #F516D7 100%);
  background-image: -webkit-linear-gradient(bottom, #6E67BE 0%, #F516D7 100%);
  background-image: -ms-linear-gradient(bottom, #6E67BE 0%, #F516D7 100%);
  background-image: linear-gradient(to bottom, #6E67BE 0%, #F516D7 100%);
}

header nav ul li {
  display: inline;
  font-family: "Impact", sans-serif;
  font-size: 3rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  vertical-align: middle;
}

header nav ul li:first-child {
  font-size: 1rem;
}

header nav ul li:hover {
  background: #000;
}

header nav ul li.dont-click a {
  color: red;
  font-size: 30px;
  vertical-align: middle;
}

header nav ul li a {
  font-family: "Impact", sans-serif;
  padding: 0 10px;
  -webkit-text-stroke: 1px black;
  text-shadow: -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 2px 2px 0 #000;
  color: orange;
  text-decoration: none;
  line-height: 100%;
}

header nav ul li a:hover {
  text-decoration: none;
}

header::after {
  width: 100%;
  height: 10px;
  background: repeating-linear-gradient(-55deg, #222, #222 2px, yellow 2px, #333 10px);
}

body > footer {
  padding: 40px 0;
  text-align: center;
  background: #000;
  color: #fff;
}

body > footer ul.footer__social-icons {
  list-style-type: none;
  margin: 20px auto;
}

body > footer ul.footer__social-icons li {
  display: inline-block;
}

body > footer ul.footer__social-icons li a {
  padding: 5px;
}

body > footer a {
  color: #fff;
  text-decoration: none;
}

body > footer a:hover {
  background: #000;
  text-decoration: none;
  color: yellow;
}

body > footer a:hover i {
  color: yellow;
}

.sidebar img {
  width: 100%;
}

.sidebar .sidebar__topics {
  background-color: cyan;
  padding: 10px;
  box-sizing: border-box;
  font-size: 10px;
}

.sidebar .sidebar__8bit img {
  width: 40px;
  height: 40px;
  float: left;
}

main {
  padding: 40px 0;
  background: #fff;
  outline: 2px dashed yellow;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 3px;
  text-shadow: none;
}

main h1, main h2, main h3 {
  color: #000;
  font-weight: 700;
}

main .recommendations {
  font-size: 12px;
  color: #333;
  font-family: Helvetica;
  overflow: hidden;
  margin: 10px 0;
  vertical-align: baseline;
  font-size: 16px;
}

main .recommendations img {
  float: left;
  height: 60px;
  width: 60px;
  margin-right: 10px;
}

main .recommendations ul li {
  display: block;
  overflow: hidden;
  padding: 10px 0;
  border-bottom: 1px solid #d72bf4;
}

main .recommendations ul li a {
  font-weight: bold;
  background: none;
  color: #006fa6;
  padding: 0;
}

main .recommendations ul li a:hover {
  text-decoration: underline;
}

.github-ribbon img {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}

.hacker-news {
  font-family: Verdana, Geneva, sans-serif;
}

.hacker-news a {
  color: #000000;
  text-decoration: none;
  background: none;
  padding: 0;
}

.hacker-news a:hover {
  text-decoration: underline;
}

.hacker-news a:visited {
  color: #828282;
  text-decoration: none;
}

.hacker-news header {
  background: #FF6600;
  padding: 2px;
  overflow: hidden;
  height: 22px;
  line-height: 22px;
  text-align: left;
  text-shadow: none;
  position: relative;
}

.hacker-news header h2 {
  float: left;
  font-size: 16px;
  padding: 0px 5px;
  line-height: 18px;
  margin: 0;
}

.hacker-news header img {
  width: 18px;
  height: 18px;
  border: 1px solid #fff;
  display: inline-block;
  float: left;
}

.hacker-news ol {
  background: #f6f6ef;
}

.hacker-news ol .votearrow {
  width: 10px;
  height: 10px;
  border: 0px;
  margin: 3px 2px 6px;
  background-size: 10px;
  background: url("/img/grayarrow.gif") no-repeat;
  display: inline-block;
  vertical-align: middle;
}

.hacker-news ol span {
  font-size: 10px;
}

.meme {
  color: #081052;
  font-family: "Courier New",Courier,mono;
  text-align: center;
  font-weight: 700;
  overflow: hidden;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px 10px 0 0;
  font-size: 14px;
  background: #D0ACD0;
}

.meme img {
  height: 50px;
  width: 50px;
  float: left;
}

.typing {
  padding: 10px 0;
}

.typing #box {
  margin: auto;
  height: 200px;
  border: 1px solid #FF6158;
  background: #FFEADD;
  overflow: hidden;
  position: relative;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 30px;
  color: #2145DE;
}

.typing #box > div {
  display: inline-block;
  position: absolute;
  width: 50px;
  text-align: center;
  transition-property: right;
  transition-duration: 100ms;
  transition-timing-function: linear;
  transition-delay: 0;
}

.typing p {
  font-size: 12px;
  font-style: italic;
}

.typing #message {
  display: block;
  text-align: center;
  position: relative;
  font-size: 100px;
  top: 0;
  line-height: 200px;
  z-index: 0;
  color: #FF6158;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.typing .panel {
  max-width: 1000px;
  margin: auto;
}

.typing .panel-body {
  padding: 0;
}

.typing .btn {
  margin-left: 10px;
}

.typing .container {
  max-width: 1000px;
}

.typing .hidden {
  display: none !important;
  visibility: hidden !important;
}

.tips {
  width: 100%;
  padding: 10px;
  border: 3px solid #0F0;
  font-size: 18px;
  margin-bottom: 10px;
}

.fixed-modal {
  cursor: pointer;
  position: fixed;
  right: 10px;
  padding: 10px;
  width: 150px;
  text-align: center;
  height: auto;
  border: 1px solid #000;
  border-left: none;
  border-radius: 8px;
  box-shadow: 0 6px 9px black;
}

.fixed-modal.slack {
  top: 30%;
  background: white;
}

.fixed-modal.newsletter {
  top: 50%;
  background: #fb5952;
}

.fixed-modal .logo {
  width: 100%;
}

.fixed-modal h3 {
  font-size: 14px;
  font-family: "Impact", sans-serif;
  text-transform: uppercase;
}

.card h4 {
  font-family: "Impact", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  -webkit-text-stroke: 1px black;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.card img {
  width: 100%;
