/* Custom, iPhone Retina */
@media only screen and (min-width: 320px) and (max-width: 479px) {
  .header__click,
  .header__pikachu,
  .alex-icon {
    display: none;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (min-width: 320px) and (max-width: 767px) {
  body > header {
    height: 157px;
    background: #fff;
  }
  header h1 {
    font-size: 1rem;
  }
  header h1 a {
    line-height: 100%;
  }
  header h1 span {
    font-size: 9px;
  }
  header nav {
    display: block;
  }
  header nav ul {
    display: block;
  }
  header nav ul li {
    display: block;
  }
  header nav ul li a {
    font-size: 2rem;
    vertical-align: middle;
    line-height: 100%;
    font-family: "Impact", sans-serif;
    width: 100%;
    display: inline-block;
  }
  header nav ul li:first-child {
    display: none;
  }
  header nav ul li.dont-click {
    display: none;
  }
  header .header__thumb,
  header .header__doge,
  header .header__nerd,
  header .header__facebook,
  header .header__otter,
  header .header__linkedin,
  header .header__random,
  header .header__glitter-cat,
  header .header__absolute {
    display: none;
  }
  .github-ribbon,
  .fixed-modal,
  .tips {
    display: none;
  }
  main .sidebar {
    display: none;
  }
  main .main {
    width: 100%;
  }
}

/* Small Devices, Tablets */
@media only screen and (min-width: 768px) and (max-width: 991px) {
  header .header__thumb {
    height: 38px;
    top: 59px;
    right: 154px;
  }
  header .header__absolute {
    display: none;
  }
  header .header__facebook {
    height: 23px;
    top: 16px;
    left: 550px;
  }
  header .header__otter {
    height: 50px;
    bottom: 61px;
    left: 7%;
  }
  header .header__glitter-cat {
    height: 24px;
  }
  header .header__nerd {
    font-size: 1rem;
    top: 40px;
  }
  header .header__doge {
    top: 5px;
    font-size: 11px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  header .header__facebook {
    height: 25px;
    top: 22px;
    left: 242px;
  }
  header .header__doge {
    font-size: 11px;
    top: 5px;
  }
  header .header__otter {
    left: 7%;
  }
  header .header__glitter-cat {
    height: 54px;
  }
}

